*{margin:0;padding:0;box-sizing:border-box}html{font-size:62.5%}body{font-family:"Roboto",Arial,sans-serif;font-size:1.6rem;color:#fff;background:#0a1621;padding-top:90px}.header{margin:0 auto;display:flex;align-items:center;max-width:100rem;opacity:1}.banner-wrapper{border-radius:5px;width:100%;position:relative;background-image:url(/static/media/banner2.2b6b4a3b.jpg);background-repeat:no-repeat;background-size:100%}.banner-wrapper::after{content:"";position:absolute;top:0;left:0;width:100%;height:100%;background:#6f93b4;opacity:0.1;border-radius:5px}.banner{padding:30vh 0 0 2.5rem;bottom:8px;left:16px}.banner__title{font-size:1.6em;letter-spacing:0.05em;line-height:1;text-transform:uppercase;margin-bottom:3rem;max-width:50rem}.banner__title-developer{display:block;font-size:3rem;font-weight:400;text-transform:none;letter-spacing:0}

.skills-wrapper{width:100%;display:flex;flex-direction:column;align-items:center;margin-bottom:5rem}.skills-wrapper:last-child{margin-bottom:0}.skills__title{margin-bottom:3rem;font-weight:400;position:relative;font-size:1.5em}.skills__list{list-style:none;display:grid;grid-template-columns:repeat(auto-fit, minmax(200px, 1fr));width:100%;grid-gap:4rem 0;gap:4rem 0}.skills__list:last-child{margin-bottom:0}.skills__list-item{display:flex;flex-direction:column;align-items:center}.skills__icon{height:6rem;margin-bottom:0.5rem}.skills__name{text-transform:uppercase;font-weight:bold;letter-spacing:0.1em;font-size:0.8em;text-align:center}

